''

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAppSelector } from '../../store/store';
import { selectIsLoggedIn } from '../../store/authSlice';
import styles from './page.module.css';

interface UserProfile {
    username: string;
    social: boolean;
    nickname: string;
    email: string;
}

interface UserRequestDTO {
    username: string;
    password?: string;
    nickname: string;
    email?: string;
}

export default function ProfilePage() {
    const router = useRouter();
    const isLoggedIn = useAppSelector(selectIsLoggedIn);
    const [profile, setProfile] = useState<UserProfile | null>(null);
    const [formData, setFormData] = useState<UserRequestDTO>({ username: '', nickname: '' });
    const [originalPassword, setOriginalPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [newPasswordConfirm, setNewPasswordConfirm] = useState('');
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        if (!isLoggedIn) {
            alert('로그인이 필요합니다.');
            router.push('/login'); // 로그인 페이지로 리다이렉트
            return;
        }

        const fetchUserProfile = async () => {
            try {
                setLoading(true);
                const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/user`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        // CSRF 토큰은 브라우저가 자동으로 포함하거나, Next.js 환경에서 별도 설정 필요
                        // 여기서는 일단 생략하고, 필요시 추가
                    },
                    credentials: 'include',
                });

                if (!response.ok) {
                    const errorData = await response.json();
                    throw new Error(errorData.message || '사용자 정보를 불러오지 못했습니다.');
                }

                const userData: UserProfile = await response.json();
                setProfile(userData);
                setFormData({
                    username: userData.username,
                    nickname: userData.nickname,
                    email: userData.email,
                });
            } catch (err: any) {
                setError(err.message);
                alert(`사용자 정보 로드 실패: ${err.message}`);
                router.push('/'); // 오류 발생 시 메인 페이지로 이동
            } finally {
                setLoading(false);
            }
        };

        fetchUserProfile();
    }, [isLoggedIn, router]);

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        if (name === 'originalPassword') setOriginalPassword(value);
        else if (name === 'newPassword') setNewPassword(value);
        else if (name === 'newPasswordConfirm') setNewPasswordConfirm(value);
    };

    const getCsrfToken = async () => {
        // CSRF 토큰을 가져오는 로직 (예: 별도 API 호출 또는 쿠키에서 읽기)
        // 실제 프로덕션 환경에서는 보안을 위해 적절한 방법 사용 필요
        // 여기서는 예시로 document.cookie에서 XSRF-TOKEN을 파싱하는 간단한 방법을 사용
        const cookies = document.cookie.split('; ');
        const csrfCookie = cookies.find(cookie => cookie.startsWith('XSRF-TOKEN='));
        return csrfCookie ? decodeURIComponent(csrfCookie.split('=')[1]) : '';
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!profile) return;

        if (!profile.social && newPassword) {
            if (!originalPassword) {
                alert('비밀번호 변경 시 현재 비밀번호를 입력해야 합니다.');
                return;
            }
            if (newPassword !== newPasswordConfirm) {
                alert('새 비밀번호와 확인 비밀번호가 일치하지 않습니다.');
                return;
            }
            // TODO: 비밀번호 유효성 검사 추가 (백엔드 스펙에 따름)
        }

        const updateData: UserRequestDTO = {
            username: profile.username,
            nickname: formData.nickname,
        };

        if (!profile.social) {
            // 자체 로그인 사용자만 비밀번호와 이메일 수정 가능
            if (newPassword) {
                updateData.password = newPassword; // 백엔드에서 현재 비밀번호 검증 필요
            }
            updateData.email = formData.email;
        } else {
            // 소셜 로그인 사용자는 이메일 수정 불가, 닉네임만 가능
            if (formData.email !== profile.email) {
                alert('소셜 로그인 사용자는 이메일을 변경할 수 없습니다.');
                return;
            }
        }

        try {
            const csrfToken = await getCsrfToken();
            const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/user`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'X-XSRF-TOKEN': csrfToken, // CSRF 보호
                },
                credentials: 'include',
                body: JSON.stringify(updateData),
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || '사용자 정보 수정에 실패했습니다.');
            }

            alert('회원 정보가 성공적으로 수정되었습니다.');
            // 성공 시 새로운 프로필 정보로 상태 업데이트
            setProfile(prev => prev ? { ...prev, ...updateData } : null);
            // 비밀번호 필드 초기화
            setOriginalPassword('');
            setNewPassword('');
            setNewPasswordConfirm('');

        } catch (err: any) {
            setError(err.message);
            alert(`회원 정보 수정 실패: ${err.message}`);
        }
    };

    const handleWithdrawClick = () => {
        router.push('/mypage/withdraw'); // 탈퇴 페이지로 이동
    };

    if (loading) {
        return <div className={styles.loadingContainer}>Loading profile...</div>;
    }

    if (error) {
        return <div className={styles.errorContainer}>Error: {error}</div>;
    }

    if (!profile) {
        return <div className={styles.noProfileContainer}>No profile data. Please log in.</div>;
    }

    const isSocialLoginUser = profile.social;

    return (
        <div className={styles.container}>
            <div className={styles.header}>
                <h2 className={styles.title}>개인 정보 수정</h2>
            </div>
            <div className={styles.content}>
                <form onSubmit={handleSubmit}>
                    {/* 아이디 */}
                    <div className={styles.inputGroup}>
                        <label htmlFor="username" className={styles.label}>아이디</label>
                        <input
                            id="username"
                            name="username"
                            type="text"
                            className={styles.inputField}
                            value={profile.username}
                            readOnly
                        />
                    </div>

                    {/* 현재 비밀번호 (자체 로그인만) */}
                    {!isSocialLoginUser && (
                        <div className={styles.inputGroup}>
                            <label htmlFor="originalPassword" className={styles.label}>현재 비밀번호</label>
                            <input
                                id="originalPassword"
                                name="originalPassword"
                                type="password"
                                className={styles.inputField}
                                placeholder="현재 비밀번호를 입력해 주세요"
                                value={originalPassword}
                                onChange={handlePasswordChange}
                                autoComplete="off"
                            />
                        </div>
                    )}

                    {/* 새 비밀번호 (자체 로그인만) */}
                    {!isSocialLoginUser && (
                        <div className={styles.inputGroup}>
                            <label htmlFor="newPassword" className={styles.label}>새 비밀번호</label>
                            <input
                                id="newPassword"
                                name="newPassword"
                                type="password"
                                className={styles.inputField}
                                placeholder="새 비밀번호를 입력해 주세요"
                                value={newPassword}
                                onChange={handlePasswordChange}
                                autoComplete="off"
                            />
                        </div>
                    )}

                    {/* 새 비밀번호 확인 (자체 로그인만) */}
                    {!isSocialLoginUser && (
                        <div className={styles.inputGroup}>
                            <label htmlFor="newPasswordConfirm" className={styles.label}>새 비밀번호 확인</label>
                            <input
                                id="newPasswordConfirm"
                                name="newPasswordConfirm"
                                type="password"
                                className={styles.inputField}
                                placeholder="새 비밀번호를 다시 입력해 주세요"
                                value={newPasswordConfirm}
                                onChange={handlePasswordChange}
                                autoComplete="off"
                            />
                        </div>
                    )}

                    {/* 닉네임 */}
                    <div className={styles.inputGroup}>
                        <label htmlFor="nickname" className={styles.label}>닉네임</label>
                        <input
                            id="nickname"
                            name="nickname"
                            type="text"
                            className={styles.inputField}
                            value={formData.nickname}
                            onChange={handleChange}
                            placeholder="닉네임을 입력해 주세요"
                        />
                    </div>

                    {/* 이메일 (자체 로그인만) */}
                    {!isSocialLoginUser && (
                        <div className={styles.inputGroup}>
                            <label htmlFor="email" className={styles.label}>이메일</label>
                            <input
                                id="email"
                                name="email"
                                type="email"
                                className={styles.inputField}
                                value={formData.email}
                                onChange={handleChange}
                                placeholder="이메일을 입력해 주세요"
                            />
                        </div>
                    )}

                    {/* 휴대폰 */}
                    <div className={styles.inputGroup}>
                        <label htmlFor="mobileNumber" className={styles.label}>휴대폰</label>
                        <input
                            id="mobileNumber"
                            name="mobileNumber"
                            type="text"
                            className={styles.inputField}
                            value="010-XXXX-XXXX" // 임시 값, 실제는 백엔드에서 받아와야 함
                            readOnly
                        />
                        <button type="button" className={styles.secondaryButton}>다른번호 인증</button>
                    </div>

                    {/* 성별 (디자인 가이드에는 있으나 백엔드 스펙에 없어 readOnly로 표시) */}
                    <div className={styles.inputGroup}>
                        <label className={styles.label}>성별</label>
                        <div className={styles.radioGroup}>
                            <label><input type="radio" name="gender" value="MALE" disabled /> 남자</label>
                            <label><input type="radio" name="gender" value="FEMALE" disabled /> 여자</label>
                            <label><input type="radio" name="gender" value="NONE" disabled checked /> 선택안함</label>
                        </div>
                    </div>

                    {/* 생년월일 (디자인 가이드에는 있으나 백엔드 스펙에 없어 readOnly로 표시) */}
                    <div className={styles.inputGroup}>
                        <label className={styles.label}>생년월일</label>
                        <div className={styles.birthDateGroup}>
                            <input type="text" name="birthYear" placeholder="YYYY" className={styles.smallInputField} readOnly />
                            <span className={styles.separator}></span>
                            <input type="text" name="birthMonth" placeholder="MM" className={styles.smallInputField} readOnly />
                            <span className={styles.separator}></span>
                            <input type="text" name="birthDay" placeholder="DD" className={styles.smallInputField} readOnly />
                        </div>
                    </div>

                    {/* 이용약관 동의 (더미) */}
                    <div className={styles.termsGroup}>
                        <label className={styles.label}>이용약관동의</label>
                        <div className={styles.checkboxGroup}>
                            <label className={styles.checkboxLabel}>
                                <input type="checkbox" className={styles.checkbox} />
                                <span>무료배송, 할인쿠폰 등 혜택/정보 수신 동의</span>
                                <span className={styles.optionalText}>(선택)</span>
                            </label>
                            <div className={styles.subCheckboxGroup}>
                                <label className={styles.checkboxLabel}>
                                    <input type="checkbox" className={styles.checkbox} />
                                    <span>SMS</span>
                                </label>
                                <label className={styles.checkboxLabel}>
                                    <input type="checkbox" className={styles.checkbox} />
                                    <span>이메일</span>
                                </label>
                            </div>
                        </div>
                    </div>

                    <div className={styles.buttonGroup}>
                        <button type="button" onClick={handleWithdrawClick} className={`${styles.button} ${styles.withdrawButton}`}>탈퇴하기</button>
                        <button type="submit" className={`${styles.button} ${styles.submitButton}`}>회원정보수정</button>
                    </div>
                </form>
            </div>
        </div>
    );
}
